@import '../Variables/Variables.scss';

.navbar {
    
    &__container {
        background: rgb(255,255,255);
        background: -moz-linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 40%, rgba(88,89,91,1) 40%, rgba(88,89,91,1) 43%, rgba(43,53,148,1) 43%, rgba(43,53,148,1) 100%);
        background: -webkit-linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 40%, rgba(88,89,91,1) 40%, rgba(88,89,91,1) 43%, rgba(43,53,148,1) 43%, rgba(43,53,148,1) 100%);
        background: linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 40%, rgba(88,89,91,1) 40%, rgba(88,89,91,1) 43%, rgba(43,53,148,1) 43%, rgba(43,53,148,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#2b3594",GradientType=1);
        padding: 1rem 6rem !important;

        @include breakpoint(mobile) {
            padding: 1rem !important;

            background: rgb(255,255,255);
            background: -moz-linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 65%, rgba(88,89,91,1) 65%, rgba(88,89,91,1) 69%, rgba(43,53,148,1) 69%, rgba(43,53,148,1) 100%);
            background: -webkit-linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 65%, rgba(88,89,91,1) 65%, rgba(88,89,91,1) 69%, rgba(43,53,148,1) 69%, rgba(43,53,148,1) 100%);
            background: linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 65%, rgba(88,89,91,1) 65%, rgba(88,89,91,1) 69%, rgba(43,53,148,1) 69%, rgba(43,53,148,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#2b3594",GradientType=1);
        }
    }

    &-brand {
        img {
            width: 100%;
        }

        @include breakpoint(mobile) {
            width: 65%;
        }
    }

    &-toggler {
        border: 0 !important;
        padding: 0 !important;
    }
}

// setting outline to none
.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
    outline: none;
    box-shadow: none;
}

.navbar-nav {

    &__nav {

        li{
            &:not(:last-of-type) {
                margin-right: 50px;
            }
        }
        a {
            font-family: $main-font;

            @include breakpoint(mobile) {
                color: $primary-color !important;
            }
        }
    }
}

// hamburger

.hamburger {
    
    .line{
        width: 35px;
        height: 3px;
        background-color: $color-white;
        display: block;
        margin: 8px 0;
        -webkit-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;

        &:first-of-type {
            width: 50px;
        }

        &:last-of-type {
            width: 25px;
        }
    }
    
    &:hover{
        cursor: pointer;
    }
}

#hamburger.is-active .line:nth-child(1){
    opacity: 0;
    -webkit-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
    -o-transform: translateX(-100%);
    transform: translateX(-100%);
}
  
#hamburger.is-active .line:nth-child(3){
    opacity: 0;
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
    -o-transform: translateX(100%);
    transform: translateX(100%);
}