@import '../Variables/Variables.scss';

.servicesHome {

    background-image: linear-gradient(45deg, #ffffff 25%, #fafafa 25%, #fafafa 50%, #ffffff 50%, #ffffff 75%, #fafafa 75%, #fafafa 100%);
    background-size: 14.14px 14.14px;

    h2, h3 {
        font-size: 1.5rem;
        font-weight: 700;
        color: $primary-color;
    }

    &__card {
        padding: 2rem !important;
        background-color: $color-white;
        transition: all 0.3s ease-in;
        

        &:not(:last-of-type) {
            position: relative;

            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 1px;
                height: 7rem;
                background-color: $secondary-color;
                top: 8rem;
                right: 0;
            } 
        }

        img {
            width: 80px;
            height: 80px;
            margin-bottom: 2rem;
        }

        h3 {
            margin-bottom: 1.3rem;
        }

        &:hover {
            background-color: $secondary-color;
            transform: translateY(-20px);
            -webkit-box-shadow: 0px 20px 0px 0px $color-yellow;
            -moz-box-shadow:    0px 20px 0px 0px $color-yellow;
            box-shadow:         0px 20px 0px 0px $color-yellow;
            cursor:default;
        }
    }

    &__content {
        box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    }
}


