@import '../Variables/Variables.scss';

.aboutUsHistory {
    overflow: hidden;

    &__content {

    
        &-left, &-right {
            width: 600px;
            margin-bottom: 8rem;

            .h3 {
                transition: color 0.3s ease-in-out;

                &:hover {
                    color: $color-gray;
                }
            }

            @include breakpoint(mobile) {
                width: 100%;
                margin-bottom: 4rem;
            }
        }
    
        @include breakpoint(mobile) {
            height: unset;
            margin-bottom: 2rem;
        }
    }
}

