@import '../Variables/Variables.scss';

.ourValuesHome {

    &__left {
        padding: 5rem;

        p {
            line-height: 2rem;
        }

        @include breakpoint(mobile) {
            padding: 2rem;
        }
    }

    &__icon {
        position: relative;

        &::before {
            content:"";
            display: block;
            width: 2rem;
            height: 2rem;
            background: url("../../Img/ServicesHome/icon.svg");
            position: absolute;
            top: 0.375rem;
            left: -2.8125rem;
        }
    }
}