@import '../Variables/Variables.scss';


.references__content-overlay {
    padding: 40px;
    top: auto;
    bottom: 0;
    opacity: 0;

    div, h4 {
        color: $color-white;
    }
}

.references__content > .references__content-overlay {

    position: absolute;
    width: 100%;
    height: auto;

    left: 0;

    display: block;
    transition: opacity 10ms 10ms ease-in-out;
}

.references {
    &__content {
        position: relative;
        cursor: pointer;
        overflow: hidden;

        &::before {
            content: "";
            display: block;
            background-color: $primary-color;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 100%;
            transition: left 200ms ease-out;
        }

        &:hover::before {
            left: 0;
        }

        &:hover .references__content-overlay {
            opacity: 1;
            transition: opacity 500ms 250ms ease-in-out;
        }
    }
}
