@import '../Variables/Variables.scss';

.hero {
    position: relative;
    width: 100%;
    height: 750px; 
    background-repeat: no-repeat;
    background-size: cover;

 

    &__content {
        padding: 0 6rem;
        
        h1 {
            font-size: 5rem;
            text-transform: uppercase;
            font-weight: 700;

            @include breakpoint(mobile) {
                font-size: 2.4rem;
            }
        }

        h3 {
            @include breakpoint(mobile) {
                font-size: 1.2rem;
            }
        }

        h1, h3 {
            width: 70%;
            color: $color-white;

            @include breakpoint(mobile) {
                width: unset;
            }
        }

        @include breakpoint(mobile){
            padding: 0 1rem;
        }
    }

    @include breakpoint(mobile) {
        height: 350px;
        background-position: center;
    }
}
