@import '../Variables/Variables.scss';

.contact {
  padding: 6rem 0;
  box-sizing: border-box;

  h1 {
    color: $primary-color;
  }

  &__content {
    margin-top: 6rem;
  }

  &__map-link {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
  }

  &__card {
    min-height: 400px;
    padding: 3rem;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    overflow: hidden;

    p {
      &:last-of-type {
        margin: 0;
      }
    }

    @include breakpoint(mobile) {
      min-height: unset;
      padding: 1.5rem;
    }
  }

  &__pin {
    position: relative;
    cursor: pointer;

    &::before {
      content:"";
      background-image: url(../../Img//Contact/pin.svg);
      display: block;
      position: absolute;
      right: -6rem;
      bottom: 0;
      width: 17rem;
      height: 17rem;
      opacity: 0.1;
      transition: all 0.4s ease-in-out;
    }

    &:hover {
      
      &::before {
        width: 24rem;
        height: 24rem;
      }
    }

  }

  &__info {
    .link-white {
      color: $color-white;

      &:hover {
        opacity: 0.7;
      }
    }
  }
}