@import '../Variables/Variables.scss';

.aboutUs {

    padding: 5rem 0;

    &__content {
        padding-left: 3rem !important;

        @include breakpoint(mobile) {
            padding-left: 1rem !important;
        }
    }

    &__img {
        @include breakpoint(mobile) {
            margin-bottom: 4rem;
        }
    }

    @include breakpoint(mobile) {
        padding-left: unset;
    }
}